






import { Action } from 'vuex-class';
import { Vue, Component } from "vue-property-decorator";

/**
 * 登入驗證轉接
 */
@Component
export default class AuthHandler extends Vue {

    /**
     * 完成登入後的動作
     */
    @Action('finalizeLogin') finalizeLogin: any;

    /**
     * 元件建立完成後呼叫
     */
    created() {
        this.finalizeLogin(window.location.hash);
    }
}
